.Friends {
  padding: 1rem;
  border-radius: 2px;
}
.title {
  display: block;
  margin-bottom: .5em;
  font-size: 1.5em;
}

.list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  flex: 1;
  margin: 0 .75rem 0.2rem 0;

  a {
    display: block;
    text-align: center;
  }
}

.ava {
  display: block;
  margin: 0 auto 0.2rem;
  padding: .5em;
  box-sizing: border-box;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
}
